.enterButton {
    position: absolute;
    z-index: 100;
    width: 300px;
    height: 100px;
    background-color: #fd805d;
    border-radius: 5px;
    bottom: 40px;
    left: 40px;
    text-align: center;
    line-height: 2.6;
    font-size: 39px;
    text-decoration: none;
    color: white;
    -webkit-box-shadow: 5px 5px 30px #888888;
            box-shadow: 5px 5px 30px #888888;
}

.deckGLMap {
    position: absolute;
    z-index: 0;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

}


